import { MODEL_NAME, SYSTEM_PROMPT } from "./constants.js";
import { HTML } from "./ui.js";

export default {
  async fetch(request, env, ctx) {
    const url = new URL(request.url);

    // --- API endpoint for chat ---
    if (url.pathname === "/api/chat" && request.method === "POST") {
      try {
        const { messages = [] } = await request.json();

        // Call Workers AI with system prompt + conversation history
        const aiResponse = await env.AI.run(MODEL_NAME, {
          messages: [
            { role: "system", content: SYSTEM_PROMPT },
            ...messages,
          ],
        });

        // Be defensive about the shape of the AI response
        const text =
          aiResponse?.response ||
          aiResponse?.output ||
          aiResponse?.output_text ||
          JSON.stringify(aiResponse);

        return new Response(JSON.stringify({ reply: text }), {
          headers: { "Content-Type": "application/json" },
        });
      } catch (err) {
        return new Response(
          JSON.stringify({ error: "Something went wrong." }),
          { status: 500, headers: { "Content-Type": "application/json" } }
        );
      }
    }

    // --- Default: serve the minimal HTML UI ---
    return new Response(HTML, {
      headers: { "Content-Type": "text/html; charset=UTF-8" },
    });
  },
};
